.pagination {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 35px;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  color: #2D2E46;
  margin-bottom: 10px;
}
.item {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  width: 24px;
  height: 24px;
  border-radius: 3px;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  line-height: 120%;
  cursor: pointer;
  margin-right: 15px;
}

.select {
  border: 1px solid black;
}


.points {
  display: flex;
  align-items: flex-end;
  margin-right: 15px;
}

.lastPaginationPage {
  border: 2px solid black;
}


.selectBlock {
  .label1,
  .label2 {
    display: inline-block;
    font-family: "Lato", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
  }

  .label1 {
    margin-left: 20px;
    margin-right: 5px;
  }

  .superSelect {
    width: 50px;
  }

  .label2 {
    margin-left: 5px;
  }
}